// 
class RecordManager {
    static recorderManager = null
    static instances = new Map()
    static isInitialized = false

    constructor(id = 'default') {
        this.id = id
        this.tempFilePath = ''
        this.isRecording = false
        this.recordTime = 0
        this.timer = null
        this.onTimeUpdate = null
        this.onVolumeUpdate = null
        this.stopResolve = null
        this.instanceId = Date.now() + Math.random()
        this.stopTimeout = null
        this.recorder = uni.getRecorderManager()

        // 
        this.recorder.onFrameRecorded((res) => {
            const volume = res.volume;  // 
            if (this.onVolumeUpdate) {
                this.onVolumeUpdate(volume);  // 
            }
        });

        // 
        RecordManager.instances.set(this.instanceId, this)

        console.log(`: ${this.id}, ID: ${this.instanceId}`)
    }
    /*
    uni.getRecorderManager() 
    uni-app  uni.getRecorderManager() RecordManager 

     onStop/onError     
     RecordManager.instances  isRecording 

     isInitialized  RecordManager  onStop/onError 
    */

    // 
    static initStaticRecorder() {
        if (!RecordManager.isInitialized) {
            console.log('')
            RecordManager.recorderManager = uni.getRecorderManager()
            
            // 
            RecordManager.recorderManager.onStop((res) => {
                console.log('', res)
                
                // 
                RecordManager.instances.forEach(instance => {
                    instance.handleStopEvent(res)
                })
            })
            
            // 
            RecordManager.recorderManager.onError((err) => {
                console.error(':', err)
                
                // 
                RecordManager.instances.forEach(instance => {
                    instance.handleErrorEvent(err)
                })
            })
            
            // 
            RecordManager.recorderManager.onFrameRecorded((res) => {
                // 
                RecordManager.instances.forEach(instance => {
                    instance.handleFrameRecordedEvent(res)
                })
            })
            
            RecordManager.isInitialized = true
        }
    }

    // 
    init() {
        // 
        RecordManager.initStaticRecorder()
        this.recorderManager = RecordManager.recorderManager
        console.log(`: ${this.id}`)
    }

    // 
    handleStopEvent(res) {
        console.log(`${this.id} isRecording: ${this.isRecording}`)

        // 
        if (this.isRecording) {
            this.isRecording = false
            this.stopTimer()
            this.tempFilePath = res.tempFilePath

            // 
            if (this.stopTimeout) {
                clearTimeout(this.stopTimeout)
                this.stopTimeout = null
            }

            if (this.stopResolve) {
                console.log(`${this.id} stopResolve`)
                this.stopResolve()
                this.stopResolve = null
            }
        }
    }

    // 
    handleErrorEvent(err) {
        console.log(`${this.id} isRecording: ${this.isRecording}`)
        
        // 
        if (this.isRecording) {
            this.isRecording = false
            this.stopTimer()
            
            // 
            if (this.stopTimeout) {
                clearTimeout(this.stopTimeout)
                this.stopTimeout = null
            }
            
            if (this.stopResolve) {
                console.log(`${this.id} stopResolve`)
                this.stopResolve()
                this.stopResolve = null
            }
            
            uni.showToast({
                title: ': ' + (err.errMsg || err.message),
                icon: 'none'
            })
        }
    }
    
    // 
    setTimeUpdateCallback(callback) {
        this.onTimeUpdate = callback
    }

    // 
    setVolumeUpdateCallback(callback) {
        this.onVolumeUpdate = callback
    }

    // 
    handleFrameRecordedEvent(res) {
        // 
        if (this.isRecording) {
            // 
            const volume = this.calculateVolume(res.frameBuffer)
            
            // 
            if (this.onVolumeUpdate) {
                this.onVolumeUpdate(volume)
            }
        }
    }

    // 
    calculateVolume(frameData) {
        if (!frameData || frameData.length === 0) {
            return 0
        }

        // 0-100
        let sum = 0
        for (let i = 0; i < frameData.length; i++) {
            sum += Math.abs(frameData[i])
        }
        
        const avg = sum / frameData.length
        // 3276816PCM
        const volume = Math.min(Math.max(Math.round((avg / 32768) * 100), 0), 100)
        
        return volume
    }

    // 
    startRecord() {
        return new Promise((resolve, reject) => {
            if (!this.recorderManager) {
                this.init()
            }

            // 
            uni.getSetting({
                success: (res) => {
                    if (!res.authSetting['scope.record']) {
                        // 
                        uni.authorize({
                            scope: 'scope.record',
                            success: () => {
                                this._doStartRecord()
                                resolve()
                            },
                            fail: () => {
                                uni.showModal({
                                    title: '',
                                    content: '',
                                    success: (modalRes) => {
                                        if (modalRes.confirm) {
                                            uni.openSetting()
                                        }
                                    }
                                })
                                reject('')
                            }
                        })
                    } else {
                        // 
                        this._doStartRecord()
                        resolve()
                    }
                },
                fail: (err) => {
                    reject(': ' + (err.errMsg || err.message))
                }
            })
        })
    }

    // 
    _doStartRecord() {
        // 
        const recordOptions = {
            // KB
            // frameRecordSizeonFrameRecorded
            //  1KB 
            frameRecordSize: 1  // 1KB50ms
        }

        // 
        try {
            this.recorderManager.start(recordOptions)
        } catch (error) {
            // 
            console.warn(`${this.id} :`, error)
            this.recorderManager.start()
        }

        this.isRecording = true
        this.startTimer()
        console.log(`${this.id} `)
    }

    // 
    stopRecord() {
        return new Promise((resolve) => {
            console.log(`${this.id} isRecording: ${this.isRecording}`)

            if (this.recorderManager && this.isRecording) {
                // resolve
                this.stopResolve = resolve

                // stop
                this.recorderManager.stop()

                // onStopPromisepending
                this.stopTimeout = setTimeout(() => {
                    console.warn(`${this.id} resolve`)

                    // 
                    this.isRecording = false
                    this.stopTimer()

                    // resolve
                    if (this.stopResolve) {
                        this.stopResolve()
                        this.stopResolve = null
                    }

                    // 
                    this.stopTimeout = null
                }, 3000) // 3

                console.log(`${this.id} recorderManager.stop()`)
            } else {
                console.log(`${this.id} resolve`)
                resolve()
            }
        })
    }

    // 
    startTimer() {
        // 
        this.stopTimer()

        this.recordTime = 0
        this.timer = setInterval(() => {
            this.recordTime++  // 
            if (this.onTimeUpdate){
                this.onTimeUpdate(this.recordTime,this.getFormattedTime())
            }
        }, 1000)
    }

    // 
    stopTimer() {
        console.log(`${this.id} `)

        if (this.timer) {
            clearInterval(this.timer)
            this.timer = null
        }

        // 
        if (this.stopTimeout) {
            clearTimeout(this.stopTimeout)
            this.stopTimeout = null
        }
    }

    // 
    getRecordFile() {
        return this.tempFilePath
    }

    // 
    getFormattedTime() {
        const minutes = Math.floor(this.recordTime / 60)
        const seconds = this.recordTime % 60
        return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`
    }

    // 
    destroy() {
        console.log(`${this.id} `)

        // 
        if (this.isRecording) {
            this.stopRecord()
        }

        // 
        this.stopTimer()

        // 
        RecordManager.instances.delete(this.instanceId)

        console.log(`${this.id} `)
    }
}

// 
const recordManagerInstance = new RecordManager()
export default recordManagerInstance

//
export { RecordManager }